import React from 'react';
import styles from './BigProjectCard.module.css';

const BigProjectCard = (props) => {

    return (
        <div className={styles.cart}>
                <img src={props.imgSrc} alt={props.imgAlt} />
                <p className={styles.project_title}>{props.name}</p>
                <span className={styles.cart_description}>{props.description}</span>
                <br />
                <button className={styles.wisit_button}>Visit Website</button>
            </div>
    );
}

export default BigProjectCard;