import React from 'react';
import s from './Experiences.module.css';

function ExperiencesCard(props) {
    return (
        <div className={s.experiences_cart}>
            <div className={s.title_div}>
                <span className={s.company_name}>{props.companyName}</span>
                <br />
                <img className={s.company_img} src={props.companyImageSrc} alt={props.companyName} />
            </div>

            <span className={s.job}>{props.job}</span>

            <span className={s.date}>{props.jobExperience}</span>

            <br />
            <span className={s.impressions}>{props.impressions}</span>
        </div>
    );
}

export default ExperiencesCard;