import React from 'react';
import TigranTumoImg from '../../assets/images/TigranTumo.jpg';
import styles from './Greeting.module.css';
import { Link } from 'react-scroll';

const Home = () => {
    let socImages = [
        {
            iconSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/91/Octicons-mark-github.svg/1200px-Octicons-mark-github.svg.png',
            iconAlt: 'github',
            href: 'https://github.com/TigranMets'
        },
        {
            iconSrc: 'https://facebookbrand.com/wp-content/uploads/2019/04/f_logo_RGB-Hex-Blue_512.png?w=512&h=512',
            iconAlt: 'facebook',
            href: ''
        },
        {
            iconSrc: 'https://icon-library.com/images/gmail-circle-icon/gmail-circle-icon-5.jpg',
            iconAlt: 'gmail',
            href: 'https://mail.google.com/mail/u/0/#inbox'
        },
        {
            iconSrc: 'https://www.instagram.com/static/images/ico/favicon-200.png/ab6eff595bb1.png',
            iconAlt: 'instagram',
            href: 'https://github.com/TigranMets'
        },

        {
            iconSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/f/f8/LinkedIn_icon_circle.svg/2048px-LinkedIn_icon_circle.svg.png',
            iconAlt: 'linkedin',
            href: 'https://github.com/TigranMets'
        },
        {
            iconSrc: 'https://www.vectorico.com/wp-content/uploads/2018/02/youtube-icon.png',
            iconAlt: 'youtube',
            href: 'https://github.com/TigranMets'
        },
        {
            iconSrc: 'https://cdn3.iconfinder.com/data/icons/popular-services-brands-vol-2/512/stackoverflow-512.png',
            iconAlt: 'stackoverflow',
            href: 'https://github.com/TigranMets'
        },

    ];
    return (
        <div className={styles.homeWrapper}>
            <div className={styles.left_block}>
                <h1 className={styles.greeting}>Hi all, I'm TigranMets 👋</h1>
                <span className={styles.aboutMe}>Frontend developer 🚀 having an experience of creating web appliactions using JS, React Redux and some other cool libraries.</span>
                <div className={styles.socImagesWrapper}>
                    <div className={styles.socImages}>
                        {socImages.map(socialIcon => <a href={socialIcon.href}><img alt={socialIcon.iconAlt} src={socialIcon.iconSrc} /></a>)}
                    </div>
                </div>
                <div className={styles.buttons}>
                    <Link to='footer' spy={true} smooth={true} ><button className={styles.button}>CONTACT ME</button></Link>
                    <a className={styles.resume} href='https://staff.am/en/users/resume/create-pdf'><button className={styles.button}>SEE MY RESUME</button></a>
                </div>
            </div >
            <img className={styles.myPhoto} src={TigranTumoImg} alt='My Photo near TUMO' />
        </div >
    );
}

export default Home;