import React from 'react';
import s from './Skills.module.css';
import Tikoit from '../../assets/images/Tikoit.jpg';
import ProgrammingLanguage from './ProgrammingLanguage/ProgrammingLanguage';

const Skills = () => {
    const programmingLanguages = [
        {
            imgSrc: 'https://webref.ru/assets/images/book/html5.png',
            imgAlt: 'HTML',
            languageName: 'HTML',
        },
        {
            imgSrc: 'https://www.toolbox.am/img/trainings/icons//css3-logo.png',
            imgAlt: 'CSS',
            languageName: 'CSS',
        },
        {
            imgSrc: 'https://html5hive.org/wp-content/uploads/2014/06/js_800x800.jpg',
            imgAlt: 'JavaScript',
            languageName: 'JavaScript',
        },
        {
            imgSrc: 'https://avatars.githubusercontent.com/u/70142?s=200&v=4',
            imgAlt: 'jQuery',
            languageName: 'jQuery',
        },
        {
            imgSrc: 'https://sap.github.io/ui5-webcomponents/assets/images/react.svg',
            imgAlt: 'React',
            languageName: 'React',
        },
        {
            imgSrc: 'https://cdn.iconscout.com/icon/free/png-512/redux-283024.png',
            imgAlt: 'Redux',
            languageName: 'Redux',
        },
        {
            imgSrc: 'https://image.flaticon.com/icons/png/512/174/174881.png',
            imgAlt: 'Wordpress',
            languageName: 'Wordpress',
        },
    ];

    return (
        <div id='skills' className={s.body}>
            <div className={s.skillsImage}>
                <img className={s.Tigran_img} src={Tikoit} alt="my_image" />
            </div>
            <div className={s.whatIDo}>
                <span className={s.header}>Ինչ եմ անում ես</span>
                <div className={s.aboutMeBlock}>
                    <span className={s.about_me}>CRAZY FULL STACK DEVELOPER WHO WANTS TO EXPLORE EVERY TECH STACK</span>
                </div>

                <div className={s.languagesWrapper}>
                    <div className={s.languages}>
                        {programmingLanguages.map(programmingLanguage => <ProgrammingLanguage imgSrc={programmingLanguage.imgSrc} imgAlt={programmingLanguage.imgAlt} languageName={programmingLanguage.languageName} />)}
                    </div>
                </div>

                <div className={s.mySkillsWrapper}>
                    <span>⚡Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ipsa iure magnam dolores vel alias odit.</span>
                    <span>⚡Lorem ipsum dolor sit amet consectetur adipisicing elit. Quod?</span>
                    <span>⚡Lorem ipsum dolor sit amet consectetur adipisicing elit. Tempora sequi aut aspernatur.</span>
                </div>
            </div>
        </div>
    );
}

export default Skills;